"use strict";

var React = require("react");
var style = require("./style");

var Toolbar = React.createClass({
  displayName: "Toolbar",

  render: function render() {
    return React.createElement(
      "div",
      {
        style: style.toolbar,
        onClick: this.props.onClick },
      this.props.children
    );
  }
});

module.exports = Toolbar;