"use strict";

var React = require("react");
var Tooltip = require("./Tooltip");
var DropdownMenu = require("./DropdownMenu");
var _ = require("lodash");

var Base = React.createClass({
  displayName: "Base",

  render: function render() {

    var tooltip, dropdownMenu;

    //tooltip
    if (true || this.props.tooltip) {
      tooltip = React.createElement(Tooltip, { content: "tooltip tooltip tooltip" });
    }

    //dropdown
    if (this.props.dropdownMenu) {

      var ddm = this.props.dropdownMenu;
      ddm = _.isArray(ddm) ? { items: ddm } : ddm;

      dropdownMenu = React.createElement(DropdownMenu, ddm);
    }

    return React.createElement(
      "div",
      { style: this.props.style },
      this.props.children,
      tooltip,
      dropdownMenu
    );
  }
});

module.exports = Base;